<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <Item v-for="(good,i) in goods" :key="i" :good="good" id=""></Item>
    </van-pull-refresh>
  </div>
</template>

<script>
import Item from "@/components/Item.vue"
export default {
  data() {
    return {
            isLoading:false
    };
  },
  components:{
      Item
  },
  props: ["goodlist", "goods"],
  methods: {
        onRefresh(){
            console.log("开始 刷新..."+this.isLoading);
            setTimeout(()=>{
                this.isLoading = false;
                this.$toast('下拉刷新成功');
                // 自定义事件
                this.$emit("changeallgoods");
            },1000)
        }
  },
  mounted() {

  }
};
</script>

