<template>
  <div class="list-box">
    <van-tabs animated swipeable sticky>
      <van-tab v-for="(tp,i) in typeList" :key="i" :title="tp.text" class="goodtb"  >
          <List :goodlist="goodList" @changeallgoods="getNewGoodList"  :goods="goodList.filter(item=>item.type.value==tp.value)"  id=""></List>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import List from "@/components/List.vue";
export default {
  data() {
    return {
      typeList: [],
      goodList:[],
    };
  },
  components: {
    List
  },
  methods:{
     getNewGoodList(){
            this.goodList.reverse();
        }
  },
  mounted() {
    this.$axios.get("/vue/getGoodTypes").then(res => {
      this.typeList = res.data.result;
    });
    this.$axios.get("/vue/getGoods").then(res=>{
      this.goodList = res.data.result;
    })

  }
};
</script>

<style lang="scss" scoped>
.list-box{
  padding-bottom: 0.5rem;
}
</style>